.icon-left {
  position: absolute;
  top: 10px;
  left: 10px;
}

.icon-right {
  position: absolute;
  bottom: 10px;
  right: 10px;
}

.season-display {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.winter {
    background-color: aliceblue;
}

.summer {
    background-color: rgb(132, 247, 247);
}

.season-display.winter i{
    color: blue;
}

.season-display.summer i{
    color:  rgb(253, 213, 152);
}